import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Container, Row, Col, Card, Button, ProgressBar, Spinner } from 'react-bootstrap';
import { useAuth } from '../context/AuthContext';
import { 
  Upload, 
  MessageSquare, 
  History, 
  Users, 
  FileText, 
  BarChart3,
  LogOut,
  Clock
} from 'lucide-react';
import { historyService, feedbackService, uploadService } from '../services/api';

const AdminDashboard = () => {
  const { user, logout } = useAuth();
  const [recentSessions, setRecentSessions] = useState([]);
  const [recentFeedback, setRecentFeedback] = useState([]);
  const [uploadStats, setUploadStats] = useState({ total_documents: 0 });
  const [loadingActivity, setLoadingActivity] = useState(true);

  const handleLogout = () => {
    logout();
  };

  useEffect(() => {
    const loadActivity = async () => {
      try {
        setLoadingActivity(true);
        // recent sessions
        const sessionsResp = await historyService.listSessions();
        const sessions = (sessionsResp.sessions || []).slice(0, 5);
        setRecentSessions(sessions);
        // recent feedback
        const fb = await feedbackService.getFeedbackStats();
        setRecentFeedback((fb.recent_feedback || []).slice().reverse());
        // uploads stats
        try {
          const up = await uploadService.listDocuments();
          setUploadStats({ total_documents: up.total_documents || up.total_chunks || 0 });
        } catch (_) {}
      } finally {
        setLoadingActivity(false);
      }
    };
    loadActivity();
  }, []);

  const features = [
    {
      title: 'Upload Documents',
      description: 'Upload and manage documents for the system',
      icon: Upload,
      link: '/upload',
      color: 'bg-emerald-500'
    },
    {
      title: 'Chat Interface',
      description: 'Interact with documents using AI-powered chat',
      icon: MessageSquare,
      link: '/chat',
      color: 'bg-teal-500'
    },
    {
      title: 'View History',
      description: 'Review chat history and user interactions',
      icon: History,
      link: '/history',
      color: 'bg-cyan-500'
    },
    {
      title: 'Document Library',
      description: 'Browse and manage all uploaded documents',
      icon: FileText,
      link: '/admin/documents',
      color: 'bg-blue-500'
    },
    {
      title: 'Analytics',
      description: 'View system analytics and usage statistics',
      icon: BarChart3,
      link: '/admin/analytics',
      color: 'bg-indigo-500'
    }
  ];

  return (
    <div className="min-vh-100 position-relative overflow-hidden" 
         style={{background: 'linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #f0f9ff 100%)'}}>
      
      {/* Modern Background Pattern */}
      <div className="position-absolute w-100 h-100 overflow-hidden">
        {/* Geometric shapes */}
        <div className="position-absolute bg-primary opacity-20 rounded-3xl rotate-45 animate-pulse" 
             style={{top: '10%', left: '10%', width: '80px', height: '80px'}}></div>
        <div className="position-absolute bg-info opacity-20 rounded-circle animate-bounce" 
             style={{top: '20%', right: '15%', width: '60px', height: '60px', animationDelay: '0.5s'}}></div>
        <div className="position-absolute bg-success opacity-20 rounded-3xl animate-pulse" 
             style={{bottom: '20%', left: '15%', width: '100px', height: '100px', animationDelay: '1s'}}></div>
        <div className="position-absolute bg-warning opacity-20 rounded-circle animate-bounce" 
             style={{bottom: '10%', right: '10%', width: '70px', height: '70px', animationDelay: '1.5s'}}></div>
        
        {/* Subtle grid */}
        <div className="position-absolute w-100 h-100 opacity-20" style={{
          backgroundImage: `url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23059669' fill-opacity='0.05'%3E%3Cpath d='M20 20c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10zm10 0c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10z'/%3E%3C/g%3E%3C/svg%3E")`
        }}></div>
      </div>

      {/* Enhanced Header */}
      <div className="bg-white bg-opacity-80 shadow-lg border-bottom position-relative" 
           style={{backdropFilter: 'blur(10px)', borderColor: '#e2e8f0'}}>
        <Container fluid className="py-4">
          <Row className="align-items-center">
            <Col md={6}>
              <div className="text-center text-md-start">
                <h1 className="display-6 fw-bold text-dark mb-2">
                  Admin Dashboard
                </h1>
                <p className="lead text-muted mb-0">
                  Welcome back, <span className="fw-bold text-primary">{user?.fullName || 'Admin'}</span>
                </p>
              </div>
            </Col>
            <Col md={6}>
              <div className="d-flex justify-content-center justify-content-md-end align-items-center gap-3">
                <div className="d-flex align-items-center bg-primary text-white px-3 py-2 rounded-3 shadow-sm">
                  <div className="bg-white bg-opacity-20 rounded-circle d-flex align-items-center justify-content-center me-2" 
                       style={{width: '32px', height: '32px'}}>
                    <span className="text-white fw-bold">A</span>
                  </div>
                  <span className="fw-semibold">Admin</span>
                </div>
                <Button 
                  variant="outline-secondary" 
                  onClick={handleLogout}
                  className="d-flex align-items-center"
                >
                  <LogOut size={18} className="me-2" />
                  Logout
                </Button>
              </div>
            </Col>
          </Row>
        </Container>
      </div>

      {/* Main Content */}
      <Container fluid className="py-4 position-relative" style={{zIndex: 10}}>
        {/* Quick Stats */}
        <Row className="g-4 mb-5">
          <Col xs={12} sm={6} lg={3}>
            <Card className="border-0 shadow-lg h-100" 
                  style={{
                    background: 'rgba(255, 255, 255, 0.9)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '16px'
                  }}>
              <Card.Body className="text-center p-4">
                <div className="d-flex justify-content-center mb-3">
                  <div className="bg-primary rounded-3 d-flex align-items-center justify-content-center shadow-sm" 
                       style={{width: '64px', height: '64px', background: 'linear-gradient(135deg, #10b981, #0d9488)'}}>
                    <FileText size={32} className="text-white" />
                  </div>
                </div>
                <h3 className="display-6 fw-bold text-dark mb-2">{uploadStats.total_documents}</h3>
                <p className="text-muted fw-semibold mb-3">Total Documents</p>
                <ProgressBar 
                  now={60} 
                  variant="primary" 
                  className="rounded-pill" 
                  style={{height: '8px'}}
                />
              </Card.Body>
            </Card>
          </Col>

          <Col xs={12} sm={6} lg={3}>
            <Card className="border-0 shadow-lg h-100" 
                  style={{
                    background: 'rgba(255, 255, 255, 0.9)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '16px'
                  }}>
              <Card.Body className="text-center p-4">
                <div className="d-flex justify-content-center mb-3">
                  <div className="bg-info rounded-3 d-flex align-items-center justify-content-center shadow-sm" 
                       style={{width: '64px', height: '64px', background: 'linear-gradient(135deg, #06b6d4, #0891b2)'}}>
                    <Users size={32} className="text-white" />
                  </div>
                </div>
                <h3 className="display-6 fw-bold text-dark mb-2">156</h3>
                <p className="text-muted fw-semibold mb-3">Active Users</p>
                <ProgressBar 
                  now={85} 
                  variant="info" 
                  className="rounded-pill" 
                  style={{height: '8px'}}
                />
              </Card.Body>
            </Card>
          </Col>

          <Col xs={12} sm={6} lg={3}>
            <Card className="border-0 shadow-lg h-100" 
                  style={{
                    background: 'rgba(255, 255, 255, 0.9)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '16px'
                  }}>
              <Card.Body className="text-center p-4">
                <div className="d-flex justify-content-center mb-3">
                  <div className="bg-success rounded-3 d-flex align-items-center justify-content-center shadow-sm" 
                       style={{width: '64px', height: '64px', background: 'linear-gradient(135deg, #22c55e, #16a34a)'}}>
                    <MessageSquare size={32} className="text-white" />
                  </div>
                </div>
                <h3 className="display-6 fw-bold text-dark mb-2">{recentSessions.length}</h3>
                <p className="text-muted fw-semibold mb-3">Recent Sessions</p>
                <ProgressBar 
                  now={70} 
                  variant="success" 
                  className="rounded-pill" 
                  style={{height: '8px'}}
                />
              </Card.Body>
            </Card>
          </Col>

          <Col xs={12} sm={6} lg={3}>
            <Card className="border-0 shadow-lg h-100" 
                  style={{
                    background: 'rgba(255, 255, 255, 0.9)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '16px'
                  }}>
              <Card.Body className="text-center p-4">
                <div className="d-flex justify-content-center mb-3">
                  <div className="bg-warning rounded-3 d-flex align-items-center justify-content-center shadow-sm" 
                       style={{width: '64px', height: '64px', background: 'linear-gradient(135deg, #f59e0b, #d97706)'}}>
                    <BarChart3 size={32} className="text-white" />
                  </div>
                </div>
                <h3 className="display-6 fw-bold text-success mb-2">98%</h3>
                <p className="text-muted fw-semibold mb-3">System Health</p>
                <ProgressBar 
                  now={98} 
                  variant="success" 
                  className="rounded-pill" 
                  style={{height: '8px'}}
                />
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Features Grid */}
        <Row className="g-4 mb-5">
          {features.map((feature, index) => {
            const IconComponent = feature.icon;
            return (
              <Col xs={12} md={6} lg={4} key={index}>
                <Card 
                  as={Link} 
                  to={feature.link}
                  className="border-0 shadow-lg h-100 text-decoration-none text-dark"
                  style={{
                    background: 'rgba(255, 255, 255, 0.9)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '16px',
                    transition: 'all 0.3s ease'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.transform = 'translateY(-8px)';
                    e.currentTarget.style.boxShadow = '0 20px 40px rgba(0, 0, 0, 0.15)';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.transform = 'translateY(0)';
                    e.currentTarget.style.boxShadow = '0 4px 20px rgba(0, 0, 0, 0.08)';
                  }}
                >
                  <Card.Body className="text-center p-4">
                    <div className="d-flex justify-content-center mb-4">
                      <div 
                        className={`rounded-3 d-flex align-items-center justify-content-center shadow-sm`}
                        style={{
                          width: '80px', 
                          height: '80px', 
                          background: feature.color.includes('emerald') ? 'linear-gradient(135deg, #10b981, #0d9488)' :
                                     feature.color.includes('teal') ? 'linear-gradient(135deg, #14b8a6, #0d9488)' :
                                     feature.color.includes('cyan') ? 'linear-gradient(135deg, #06b6d4, #0891b2)' :
                                     feature.color.includes('blue') ? 'linear-gradient(135deg, #3b82f6, #1d4ed8)' :
                                     'linear-gradient(135deg, #8b5cf6, #7c3aed)'
                        }}
                      >
                        <IconComponent size={40} className="text-white" />
                      </div>
                    </div>
                    <h5 className="fw-bold text-dark mb-3">{feature.title}</h5>
                    <p className="text-muted mb-4">{feature.description}</p>
                    <div className="d-flex justify-content-center">
                      <div 
                        className="rounded-pill" 
                        style={{
                          width: '40px', 
                          height: '4px', 
                          background: 'linear-gradient(135deg, #10b981, #0d9488)'
                        }}
                      ></div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            );
          })}
        </Row>

        {/* Recent Activity */}
        <Card className="border-0 shadow-lg" 
              style={{
                background: 'rgba(255, 255, 255, 0.9)',
                backdropFilter: 'blur(10px)',
                borderRadius: '16px'
              }}>
          <Card.Header className="bg-transparent border-bottom-0 pb-0">
            <div className="d-flex align-items-center">
              <BarChart3 size={24} className="text-primary me-3" />
              <h4 className="fw-bold text-dark mb-0">Recent Activity</h4>
            </div>
          </Card.Header>
          <Card.Body className="p-4">
            {loadingActivity ? (
              <div className="text-center py-5">
                <Spinner animation="border" variant="primary" className="mb-3" />
                <p className="text-muted fw-medium">Loading recent activity...</p>
              </div>
            ) : (
              <div className="space-y-3">
                {recentSessions.map((s, index) => (
                  <div key={s.session_id} 
                       className="d-flex align-items-center p-3 border rounded-3 bg-light bg-opacity-50"
                       style={{
                         borderColor: '#e2e8f0',
                         transition: 'all 0.3s ease'
                       }}
                       onMouseEnter={(e) => {
                         e.currentTarget.style.backgroundColor = 'rgba(16, 185, 129, 0.05)';
                         e.currentTarget.style.borderColor = '#10b981';
                       }}
                       onMouseLeave={(e) => {
                         e.currentTarget.style.backgroundColor = 'rgba(248, 250, 252, 0.5)';
                         e.currentTarget.style.borderColor = '#e2e8f0';
                       }}>
                    <div className="bg-primary rounded-circle me-3" 
                         style={{width: '12px', height: '12px'}}></div>
                    <div className="flex-grow-1">
                      <p className="fw-bold text-dark mb-1">
                        Chat session updated  {s.message_count} messages
                      </p>
                      <small className="text-muted">
                        <Clock size={14} className="me-1" />
                        {new Date(s.last_activity).toLocaleString()}
                      </small>
                    </div>
                  </div>
                ))}
                {recentFeedback.map((f, idx) => (
                  <div key={idx} 
                       className="d-flex align-items-center p-3 border rounded-3 bg-light bg-opacity-50"
                       style={{
                         borderColor: '#e2e8f0',
                         transition: 'all 0.3s ease'
                       }}
                       onMouseEnter={(e) => {
                         e.currentTarget.style.backgroundColor = 'rgba(6, 182, 212, 0.05)';
                         e.currentTarget.style.borderColor = '#06b6d4';
                       }}
                       onMouseLeave={(e) => {
                         e.currentTarget.style.backgroundColor = 'rgba(248, 250, 252, 0.5)';
                         e.currentTarget.style.borderColor = '#e2e8f0';
                       }}>
                    <div className="bg-info rounded-circle me-3" 
                         style={{width: '12px', height: '12px'}}></div>
                    <div className="flex-grow-1">
                      <p className="fw-bold text-dark mb-1">
                        Feedback received  rating {f.rating}{f.comment ? `  "${f.comment}"` : ''}
                      </p>
                      <small className="text-muted">
                        <Clock size={14} className="me-1" />
                        {new Date(f.timestamp).toLocaleString()}
                      </small>
                    </div>
                  </div>
                ))}
                {recentSessions.length === 0 && recentFeedback.length === 0 && (
                  <div className="text-center py-5">
                    <BarChart3 size={64} className="text-muted mb-3" />
                    <p className="text-muted fw-medium">No recent activity</p>
                  </div>
                )}
              </div>
            )}
          </Card.Body>
        </Card>
      </Container>
    </div>
  );
};

export default AdminDashboard;
