import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { Eye, EyeOff, Mail, Lock, User } from 'lucide-react';
import { Container, Row, Col, Card, Form, Button, Alert } from 'react-bootstrap';

const RegisterPage = () => {
  const navigate = useNavigate();
  const { register } = useAuth();

  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    password: '',
    confirmPassword: '',
    role: 'student'
  });

  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');

  const validatePassword = (password) => {
    const minLength = password.length >= 8;
    const hasUpperCase = /[A-Z]/.test(password);
    const hasLowerCase = /[a-z]/.test(password);
    const hasNumber = /\d/.test(password);
    const hasSpecialChar = /[!@#$%^&*(),.?":{}|<>]/.test(password);
    return minLength && hasUpperCase && hasLowerCase && hasNumber && hasSpecialChar;
  };

  const validateForm = () => {
    if (!formData.fullName.trim()) return setError('Full Name is required');
    if (!formData.email.trim()) return setError('Email is required');
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) return setError('Enter a valid email');
    if (!formData.password) return setError('Password is required');
    if (!validatePassword(formData.password)) return setError('Password must be at least 8 chars with uppercase, lowercase, number, special char');
    if (formData.password !== formData.confirmPassword) return setError('Passwords do not match');
    return true;
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    if (error) setError('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) return;
    setLoading(true);
    try {
      const result = await register(formData);
      if (result.success) {
        setSuccessMessage('Registration successful! Redirecting...');
        setTimeout(() => navigate('/login'), 2000);
      } else setError(result.error);
    } catch {
      setError('Unexpected error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-vh-100 d-flex align-items-center justify-content-center position-relative overflow-hidden" 
         style={{background: 'linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #f0f9ff 100%)'}}>

      {/* Modern Background Pattern */}
      <div className="position-absolute w-100 h-100 overflow-hidden">
        {/* Geometric shapes - responsive sizing */}
        <div className="position-absolute bg-primary opacity-20 rounded-3xl rotate-45 animate-pulse" 
             style={{top: '10%', left: '10%', width: '80px', height: '80px', animationDelay: '0s'}}></div>
        <div className="position-absolute bg-info opacity-20 rounded-circle animate-bounce" 
             style={{top: '20%', right: '15%', width: '60px', height: '60px', animationDelay: '0.5s'}}></div>
        <div className="position-absolute bg-success opacity-20 rounded-3xl animate-pulse" 
             style={{bottom: '20%', left: '15%', width: '100px', height: '100px', animationDelay: '1s'}}></div>
        <div className="position-absolute bg-warning opacity-20 rounded-circle animate-bounce" 
             style={{bottom: '10%', right: '10%', width: '70px', height: '70px', animationDelay: '1.5s'}}></div>
        
        {/* Subtle grid */}
        <div className="position-absolute w-100 h-100 opacity-20" style={{
          backgroundImage: `url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23059669' fill-opacity='0.05'%3E%3Cpath d='M20 20c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10zm10 0c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10z'/%3E%3C/g%3E%3C/svg%3E")`
        }}></div>
      </div>

      {/* Main Content Container */}
      <Container className="position-relative" style={{zIndex: 10}}>
        <Row className="justify-content-center">
          <Col xs={12} sm={10} md={8} lg={6} xl={5}>
            {/* Header Section */}
            <div className="text-center mb-4 mb-md-5">
              {/* Modern Logo */}
              <div className="mx-auto d-flex align-items-center justify-content-center mb-3 mb-md-4 shadow-lg" 
                   style={{
                     width: '80px', 
                     height: '80px', 
                     background: 'linear-gradient(135deg, #10b981, #0d9488)',
                     borderRadius: '20px',
                     transition: 'transform 0.3s ease'
                   }}
                   onMouseEnter={(e) => e.target.style.transform = 'scale(1.05)'}
                   onMouseLeave={(e) => e.target.style.transform = 'scale(1)'}>
                <User className="text-white" size={32} />
              </div>
              
              <h1 className="display-6 fw-bold text-dark mb-2">Create Your Account</h1>
              <p className="lead text-muted">Join SmartDoc to explore intelligent document solutions</p>
            </div>

            {/* Registration Form Card */}
            <Card className="border-0 shadow-lg" style={{
              background: 'rgba(255, 255, 255, 0.95)',
              backdropFilter: 'blur(20px)',
              borderRadius: '20px'
            }}>
              <Card.Body className="p-4 p-md-5">
                <Form onSubmit={handleSubmit}>
                  {/* Success / Error Alerts */}
                  {successMessage && (
                    <Alert variant="success" className="border-0 rounded-3 mb-4" style={{background: 'rgba(34, 197, 94, 0.1)'}}>
                      <div className="d-flex align-items-center">
                        <svg className="text-success me-2" width="20" height="20" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                        </svg>
                        <span className="fw-medium">{successMessage}</span>
                      </div>
                    </Alert>
                  )}

                  {error && (
                    <Alert variant="danger" className="border-0 rounded-3 mb-4" style={{background: 'rgba(239, 68, 68, 0.1)'}}>
                      <div className="d-flex align-items-center">
                        <svg className="text-danger me-2" width="20" height="20" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                        </svg>
                        <span className="fw-medium">{error}</span>
                      </div>
                    </Alert>
                  )}

                  {/* Full Name */}
                  <Form.Group className="mb-3">
                    <Form.Label className="fw-semibold text-dark">Full Name</Form.Label>
                    <div className="position-relative">
                      <div className="position-absolute top-50 start-0 translate-middle-y ms-3">
                        <User className="text-muted" size={20} />
                      </div>
                      <Form.Control
                        type="text"
                        name="fullName"
                        value={formData.fullName}
                        onChange={handleInputChange}
                        placeholder="John Doe"
                        className="ps-5"
                        style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                      />
                    </div>
                  </Form.Group>

                  {/* Email */}
                  <Form.Group className="mb-3">
                    <Form.Label className="fw-semibold text-dark">Email</Form.Label>
                    <div className="position-relative">
                      <div className="position-absolute top-50 start-0 translate-middle-y ms-3">
                        <Mail className="text-muted" size={20} />
                      </div>
                      <Form.Control
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleInputChange}
                        placeholder="you@example.com"
                        className="ps-5"
                        style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                      />
                    </div>
                  </Form.Group>

                  {/* Role */}
                  <Form.Group className="mb-3">
                    <Form.Label className="fw-semibold text-dark">Role</Form.Label>
                    <Form.Select
                      name="role"
                      value={formData.role}
                      onChange={handleInputChange}
                      style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                    >
                      <option value="student">Student</option>
                      <option value="guest">Guest</option>
                    </Form.Select>
                  </Form.Group>

                  {/* Password */}
                  <Form.Group className="mb-3">
                    <Form.Label className="fw-semibold text-dark">Password</Form.Label>
                    <div className="position-relative">
                      <div className="position-absolute top-50 start-0 translate-middle-y ms-3">
                        <Lock className="text-muted" size={20} />
                      </div>
                      <Form.Control
                        type={showPassword ? 'text' : 'password'}
                        name="password"
                        value={formData.password}
                        onChange={handleInputChange}
                        placeholder="********"
                        className="ps-5 pe-5"
                        style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                      />
                      <Button
                        type="button"
                        variant="link"
                        className="position-absolute top-50 end-0 translate-middle-y me-3 p-0"
                        onClick={() => setShowPassword(!showPassword)}
                        style={{border: 'none', background: 'none'}}
                      >
                        {showPassword ? <EyeOff className="text-muted" size={20} /> : <Eye className="text-muted" size={20} />}
                      </Button>
                    </div>
                  </Form.Group>

                  {/* Confirm Password */}
                  <Form.Group className="mb-4">
                    <Form.Label className="fw-semibold text-dark">Confirm Password</Form.Label>
                    <div className="position-relative">
                      <div className="position-absolute top-50 start-0 translate-middle-y ms-3">
                        <Lock className="text-muted" size={20} />
                      </div>
                      <Form.Control
                        type={showConfirmPassword ? 'text' : 'password'}
                        name="confirmPassword"
                        value={formData.confirmPassword}
                        onChange={handleInputChange}
                        placeholder="Re-enter password"
                        className="ps-5 pe-5"
                        style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                      />
                      <Button
                        type="button"
                        variant="link"
                        className="position-absolute top-50 end-0 translate-middle-y me-3 p-0"
                        onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                        style={{border: 'none', background: 'none'}}
                      >
                        {showConfirmPassword ? <EyeOff className="text-muted" size={20} /> : <Eye className="text-muted" size={20} />}
                      </Button>
                    </div>
                  </Form.Group>

                  {/* Submit Button */}
                  <Button
                    type="submit"
                    disabled={loading}
                    className="w-100 py-3 fw-semibold border-0"
                    style={{
                      background: 'linear-gradient(135deg, #10b981, #0d9488)',
                      borderRadius: '12px',
                      boxShadow: '0 4px 15px rgba(16, 185, 129, 0.3)'
                    }}
                  >
                    {loading ? (
                      <>
                        <div className="spinner-border spinner-border-sm me-2" role="status">
                          <span className="visually-hidden">Loading...</span>
                        </div>
                        Creating Account...
                      </>
                    ) : (
                      <>
                        Create Account
                        <svg className="ms-2" width="16" height="16" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                        </svg>
                      </>
                    )}
                  </Button>

                  {/* Login Link */}
                  <div className="text-center mt-3">
                    <p className="text-muted mb-0">
                      Already have an account?{' '}
                      <Link to="/login" className="text-decoration-none fw-semibold" style={{color: '#10b981'}}>
                        Login here
                      </Link>
                    </p>
                  </div>
                </Form>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default RegisterPage;
