import React, { useState, useRef, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Form, Spinner, Badge } from 'react-bootstrap';
import { Send, FileText, MessageCircle } from 'lucide-react';
import { chatService, feedbackService, historyService } from '../services/api';
import { useChat } from '../context/ChatContext';
import ChatMessage from '../components/ChatMessage';
import FeedbackModal from '../components/FeedbackModal';


const ChatPage = () => {
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [showFeedback, setShowFeedback] = useState(false);
  const [currentResponse, setCurrentResponse] = useState(null);
  const messagesEndRef = useRef(null);
  
  const { 
    messages, 
    addMessage, 
    sessionId, 
    currentDocument, 
    setLoading,
    setError 
  } = useChat();

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!inputValue.trim() || isLoading) return;

    const question = inputValue.trim();
    setInputValue('');
    setIsLoading(true);
    setLoading(true);

    // Add user message
    const userMessage = {
      id: Date.now(),
      type: 'user',
      content: question,
      timestamp: new Date(),
    };
    addMessage(userMessage);

    try {
      // Send to API
      const response = await chatService.sendMessage(
        question, 
        sessionId, 
        currentDocument?.id
      );

      // Add AI response
      const aiMessage = {
        id: String(response.question_id || (Date.now() + 1)),
        type: 'ai',
        content: response.answer,
        sources: response.sources,
        timestamp: new Date(response.timestamp),
        sessionId: response.session_id,
      };
      addMessage(aiMessage);
      setCurrentResponse(aiMessage);

      // Persist to history (question and answer together)
      try {
        await historyService.saveHistory({
          session_id: sessionId,
          question: question,
          answer: response.answer,
          timestamp: new Date().toISOString(),
          sources: response.sources || [],
        });
      } catch (e) {
        console.error('Failed to save chat history:', e);
      }

    } catch (error) {
      console.error('Chat error:', error);
      setError(error.message || 'Failed to get response');
      
      // Add error message
      const errorMessage = {
        id: Date.now() + 1,
        type: 'error',
        content: 'Sorry, I encountered an error while processing your question. Please try again.',
        timestamp: new Date(),
      };
      addMessage(errorMessage);
    } finally {
      setIsLoading(false);
      setLoading(false);
    }
  };

  const handleFeedback = async (rating, comment = '') => {
    if (!currentResponse) return;

    try {
      await feedbackService.submitFeedback({
        session_id: sessionId,
        question_id: String(currentResponse.id),
        rating,
        comment,
      });
      setShowFeedback(false);
      setCurrentResponse(null);
    } catch (error) {
      console.error('Feedback error:', error);
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
  };

  if (!currentDocument) {
    return (
      <Container className="py-5">
        <Row className="justify-content-center">
          <Col xs={12} md={8} lg={6}>
            <Card className="border-0 shadow-lg text-center" 
                  style={{
                    background: 'rgba(255, 255, 255, 0.9)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '16px'
                  }}>
              <Card.Body className="p-5">
                <FileText size={64} className="text-muted mb-4" />
                <h3 className="fw-bold text-dark mb-3">No Document Uploaded</h3>
                <p className="text-muted mb-4">
                  Please upload a document first to start asking questions.
                </p>
                <Button 
                  variant="primary" 
                  size="lg"
                  onClick={() => window.location.href = '/'}
                  style={{
                    background: 'linear-gradient(135deg, #10b981, #0d9488)',
                    border: 'none',
                    borderRadius: '12px'
                  }}
                >
                  Upload Document
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    );
  }

  return (
    <Container fluid className="py-4">
      {/* Document Info */}
      <Row className="mb-4">
        <Col>
          <Card className="border-0 shadow-sm" 
                style={{
                  background: 'rgba(255, 255, 255, 0.9)',
                  backdropFilter: 'blur(10px)',
                  borderRadius: '12px'
                }}>
            <Card.Body className="p-4">
              <Row className="align-items-center">
                <Col xs="auto">
                  <div className="bg-primary rounded-3 d-flex align-items-center justify-content-center" 
                       style={{width: '48px', height: '48px'}}>
                    <FileText size={24} className="text-white" />
                  </div>
                </Col>
                <Col>
                  <h5 className="fw-bold text-dark mb-1">{currentDocument.name}</h5>
                  <p className="text-muted mb-0">
                    {(currentDocument.size / 1024 / 1024).toFixed(2)} MB
                  </p>
                </Col>
                <Col xs="auto">
                  <div className="d-flex align-items-center">
                    <div className="bg-success rounded-circle me-2" 
                         style={{width: '8px', height: '8px'}}></div>
                    <Badge bg="success" className="rounded-pill">Ready for questions</Badge>
                  </div>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Chat Messages */}
      <Row className="mb-4">
        <Col>
          <Card className="border-0 shadow-lg" 
                style={{
                  background: 'rgba(255, 255, 255, 0.9)',
                  backdropFilter: 'blur(10px)',
                  borderRadius: '16px',
                  height: '500px'
                }}>
            <Card.Body className="p-0 d-flex flex-column">
              <div className="flex-grow-1 overflow-auto p-4" style={{maxHeight: '400px'}}>
                {messages.length === 0 ? (
                  <div className="text-center py-5">
                    <MessageCircle size={64} className="text-muted mb-4" />
                    <h4 className="fw-bold text-dark mb-3">Start asking questions</h4>
                    <p className="text-muted">
                      Ask anything about your uploaded document and get AI-powered answers.
                    </p>
                  </div>
                ) : (
                  <div>
                    {messages.map((message) => (
                      <ChatMessage
                        key={message.id}
                        message={message}
                        onFeedback={() => {
                          setCurrentResponse(message);
                          setShowFeedback(true);
                        }}
                        onCopy={() => copyToClipboard(message.content)}
                      />
                    ))}
                    {isLoading && (
                      <div className="d-flex align-items-center p-3 bg-light rounded-3 mb-3">
                        <Spinner animation="border" size="sm" variant="primary" className="me-3" />
                        <span className="text-muted">AI is thinking...</span>
                      </div>
                    )}
                    <div ref={messagesEndRef} />
                  </div>
                )}
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Input Form */}
      <Row>
        <Col>
          <Card className="border-0 shadow-sm" 
                style={{
                  background: 'rgba(255, 255, 255, 0.9)',
                  backdropFilter: 'blur(10px)',
                  borderRadius: '12px'
                }}>
            <Card.Body className="p-4">
              <Form onSubmit={handleSubmit}>
                <Row className="align-items-end">
                  <Col>
                    <Form.Group>
                      <Form.Control
                        type="text"
                        value={inputValue}
                        onChange={(e) => setInputValue(e.target.value)}
                        placeholder="Ask a question about your document..."
                        disabled={isLoading}
                        style={{
                          borderRadius: '12px',
                          border: '2px solid #e5e7eb',
                          padding: '12px 16px'
                        }}
                      />
                    </Form.Group>
                    <small className="text-muted">
                      Press Enter to send, or click the send button
                    </small>
                  </Col>
                  <Col xs="auto">
                    <Button
                      type="submit"
                      disabled={!inputValue.trim() || isLoading}
                      variant="primary"
                      size="lg"
                      className="px-4"
                      style={{
                        background: 'linear-gradient(135deg, #10b981, #0d9488)',
                        border: 'none',
                        borderRadius: '12px'
                      }}
                    >
                      <Send size={20} />
                    </Button>
                  </Col>
                </Row>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Feedback Modal */}
      {showFeedback && currentResponse && (
        <FeedbackModal
          onClose={() => setShowFeedback(false)}
          onSubmit={handleFeedback}
        />
      )}
    </Container>
  );
};

export default ChatPage; 