import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { 
  MessageSquare, 
  History, 
  FileText, 
  Eye,
  LogOut,
  Info
} from 'lucide-react';
import { historyService, uploadService } from '../services/api';

const GuestDashboard = () => {
  const { user, logout } = useAuth();
  const [stats, setStats] = useState({
    publicResponses: 0,
    publicDocuments: 0,
    topicsCovered: 0
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      
      // Fetch history data
      const historyData = await historyService.getHistory();
      
      // Fetch documents data
      const documentsData = await uploadService.listDocuments();
      
      // Calculate stats
      const publicResponses = Math.floor(historyData.history.length / 2); // Assuming Q&A pairs
      const publicDocuments = documentsData.documents?.length || 0;
      
      // Count unique topics (simplified - based on unique session IDs)
      const uniqueSessions = new Set(historyData.history.map(item => item.session_id));
      const topicsCovered = uniqueSessions.size;
      
      setStats({
        publicResponses,
        publicDocuments,
        topicsCovered
      });
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
      // Keep default stats on error
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    logout();
  };

  const features = [
    {
      title: 'Public History',
      description: 'View public chat history and conversations',
      icon: History,
      link: '/guest/history',
      color: 'bg-purple-500'
    },
    {
      title: 'Browse Documents',
      description: 'View publicly available documents',
      icon: FileText,
      link: '/guest/documents',
      color: 'bg-blue-500'
    },
    {
      title: 'Interactive Demo',
      description: 'Try with sample docs (3 free questions)',
      icon: Eye,
      link: '/guest/demo',
      color: 'bg-indigo-500'
    }
  ];

  const [recentResponses, setRecentResponses] = useState([]);

  const fetchRecentResponses = useCallback(async () => {
    try {
      const historyData = await historyService.getHistory();
      
      // Transform history into response format (limit to 3 most recent)
      const publicResponses = [];
      const sessionMap = new Map();
      
      // Group messages by session
      historyData.history.forEach(item => {
        if (!sessionMap.has(item.session_id)) {
          sessionMap.set(item.session_id, {
            session_id: item.session_id,
            messages: [],
            last_activity: item.timestamp
          });
        }
        sessionMap.get(item.session_id).messages.push(item);
      });
      
      // Convert sessions to Q&A pairs
      sessionMap.forEach(session => {
        const messages = session.messages.sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp));
        
        for (let i = 0; i < messages.length; i += 2) {
          if (i + 1 < messages.length) {
            const question = messages[i];
            const answer = messages[i + 1];
            
            if (question.role === 'user' && answer.role === 'assistant') {
              publicResponses.push({
                id: `${session.session_id}_${i}`,
                question: question.content,
                answer: answer.content,
                document: question.sources?.[0]?.document || 'Unknown Document',
                date: formatTimeAgo(question.timestamp)
              });
            }
          }
        }
      });
      
      // Sort by date (newest first) and take first 3
      publicResponses.sort((a, b) => new Date(b.date) - new Date(a.date));
      setRecentResponses(publicResponses.slice(0, 3));
    } catch (error) {
      console.error('Error fetching recent responses:', error);
      // Keep empty array on error
    }
  }, []);

  useEffect(() => {
    fetchRecentResponses();
  }, [fetchRecentResponses]);

  const formatTimeAgo = (timestamp) => {
    const now = new Date();
    const responseTime = new Date(timestamp);
    const diffInHours = Math.floor((now - responseTime) / (1000 * 60 * 60));
    
    if (diffInHours < 1) return 'Just now';
    if (diffInHours < 24) return `${diffInHours} hours ago`;
    const diffInDays = Math.floor(diffInHours / 24);
    if (diffInDays < 7) return `${diffInDays} days ago`;
    return responseTime.toLocaleDateString();
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-purple-50 to-pink-50">
      {/* Enhanced Header */}
      <div className="bg-white/80 backdrop-blur-xl shadow-soft border-b border-white/20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-8">
            <div className="animate-slide-right">
              <h1 className="text-4xl font-bold gradient-text text-shadow mb-2">Guest Dashboard</h1>
              <p className="text-lg text-gray-600 font-medium">
                Welcome, <span className="font-semibold text-purple-600">{user?.fullName || 'Guest'}</span>
              </p>
            </div>
            <div className="flex items-center space-x-6 animate-slide-left">
              <div className="flex items-center space-x-3 px-4 py-2 bg-gradient-to-r from-purple-500 to-pink-600 rounded-2xl shadow-glow-purple">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center">
                  <span className="text-white text-lg font-bold">G</span>
                </div>
                <span className="text-white font-semibold">Guest</span>
              </div>
              <button
                onClick={handleLogout}
                className="flex items-center space-x-2 px-4 py-2 text-gray-600 hover:text-gray-800 hover:bg-gray-100 rounded-xl transition-all duration-200 font-medium"
              >
                <LogOut className="h-5 w-5" />
                <span>Logout</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Guest Notice */}
        <div className="bg-gradient-to-r from-blue-50 to-indigo-50 border border-blue-200/50 rounded-2xl p-6 mb-12 animate-slide-down">
          <div className="flex items-start">
            <div className="flex-shrink-0">
              <div className="w-12 h-12 bg-gradient-to-r from-blue-500 to-indigo-600 rounded-2xl flex items-center justify-center">
                <Info className="h-6 w-6 text-white" />
              </div>
            </div>
            <div className="ml-4">
              <h3 className="text-lg font-bold text-blue-900 mb-2">
                Guest Access
              </h3>
              <div className="text-blue-800 font-medium">
                <p>
                  As a guest, you can view public responses and browse available documents. 
                  To upload documents or start new chat sessions, please register as a student.
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Quick Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-12">
          <div className="stat-card animate-scale-in" style={{animationDelay: '0.1s'}}>
            <div className="flex items-center justify-center mb-4">
              <div className="w-16 h-16 bg-gradient-to-r from-purple-500 to-pink-600 rounded-2xl flex items-center justify-center shadow-glow-purple">
                <Eye className="h-8 w-8 text-white" />
              </div>
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">
              {loading ? '...' : stats.publicResponses}
            </h3>
            <p className="text-gray-600 font-semibold">Public Responses</p>
            <div className="mt-3 w-full bg-gray-200 rounded-full h-2">
              <div className="bg-gradient-to-r from-purple-500 to-pink-600 h-2 rounded-full" style={{width: '70%'}}></div>
            </div>
          </div>

          <div className="stat-card animate-scale-in" style={{animationDelay: '0.2s'}}>
            <div className="flex items-center justify-center mb-4">
              <div className="w-16 h-16 bg-gradient-to-r from-blue-500 to-indigo-600 rounded-2xl flex items-center justify-center shadow-glow">
                <FileText className="h-8 w-8 text-white" />
              </div>
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">
              {loading ? '...' : stats.publicDocuments}
            </h3>
            <p className="text-gray-600 font-semibold">Public Documents</p>
            <div className="mt-3 w-full bg-gray-200 rounded-full h-2">
              <div className="bg-gradient-to-r from-blue-500 to-indigo-600 h-2 rounded-full" style={{width: '50%'}}></div>
            </div>
          </div>

          <div className="stat-card animate-scale-in" style={{animationDelay: '0.3s'}}>
            <div className="flex items-center justify-center mb-4">
              <div className="w-16 h-16 bg-gradient-to-r from-green-500 to-emerald-600 rounded-2xl flex items-center justify-center shadow-glow-green">
                <MessageSquare className="h-8 w-8 text-white" />
              </div>
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">
              {loading ? '...' : stats.topicsCovered}
            </h3>
            <p className="text-gray-600 font-semibold">Topics Covered</p>
            <div className="mt-3 w-full bg-gray-200 rounded-full h-2">
              <div className="bg-gradient-to-r from-green-500 to-emerald-600 h-2 rounded-full" style={{width: '80%'}}></div>
            </div>
          </div>
        </div>

        {/* Features Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-12">
          {features.map((feature, index) => {
            const IconComponent = feature.icon;
            return (
              <Link
                key={index}
                to={feature.link}
                className="feature-card animate-slide-up"
                style={{animationDelay: `${0.4 + index * 0.1}s`}}
              >
                <div className="p-8 text-center">
                  <div className="flex justify-center mb-6">
                    <div className={`feature-icon w-20 h-20 ${feature.color} p-5 rounded-3xl shadow-lg`}>
                      <IconComponent className="h-10 w-10 text-white" />
                    </div>
                  </div>
                  <h3 className="text-xl font-bold text-gray-900 mb-3">
                    {feature.title}
                  </h3>
                  <p className="text-gray-600 font-medium leading-relaxed">
                    {feature.description}
                  </p>
                  <div className="mt-6 flex justify-center">
                    <div className="w-8 h-1 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full"></div>
                  </div>
                </div>
              </Link>
            );
          })}
        </div>

        {/* Public Responses */}
        <div className="card animate-slide-up" style={{animationDelay: '0.7s'}}>
          <div className="px-8 py-6 border-b border-gray-200/50">
            <h3 className="text-2xl font-bold text-gray-900 flex items-center">
              <MessageSquare className="h-6 w-6 text-purple-600 mr-3" />
              Recent Public Responses
            </h3>
            <p className="text-gray-600 font-medium mt-2">Browse questions and answers from the community</p>
          </div>
          <div className="p-8">
            <div className="space-y-6">
              {recentResponses.length === 0 ? (
                <div className="text-center py-12">
                  <MessageSquare className="mx-auto h-16 w-16 text-gray-400 mb-4" />
                  <p className="text-lg text-gray-500 font-medium">No public responses available yet</p>
                </div>
              ) : (
                recentResponses.map((response, index) => (
                <div key={response.id} className="border border-gray-200/50 rounded-2xl p-6 hover:bg-gray-50/50 transition-all duration-200 hover:shadow-soft animate-slide-up" style={{animationDelay: `${0.8 + index * 0.1}s`}}>
                  <div className="flex items-start justify-between mb-4">
                    <h4 className="text-xl font-bold text-gray-900">{response.question}</h4>
                    <span className="text-sm text-gray-500 font-medium bg-gray-100 px-3 py-1 rounded-full">{response.date}</span>
                  </div>
                  <p className="text-gray-700 mb-4 font-medium leading-relaxed">{response.answer}</p>
                  <div className="flex items-center text-sm text-gray-500 font-medium">
                    <FileText className="h-4 w-4 mr-2" />
                    <span>From: {response.document}</span>
                  </div>
                </div>
                ))
              )}
            </div>
            
            <div className="mt-8 text-center">
              <Link
                to="/guest/responses"
                className="btn-accent px-6 py-3"
              >
                View All Public Responses
              </Link>
            </div>
          </div>
        </div>

        {/* Upgrade Notice */}
        <div className="mt-12 bg-gradient-to-r from-purple-500 via-pink-500 to-indigo-600 rounded-2xl p-8 text-white animate-slide-up" style={{animationDelay: '1.0s'}}>
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-2xl font-bold mb-2">Want to do more?</h3>
              <p className="text-purple-100 font-medium text-lg">
                Register as a student to upload documents and start your own chat sessions.
              </p>
            </div>
            <Link
              to="/register"
              className="bg-white text-purple-600 px-6 py-3 rounded-xl text-lg font-bold hover:bg-gray-100 transition-all duration-200 transform hover:scale-105 shadow-lg"
            >
              Register as Student
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GuestDashboard;
