import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { Eye, EyeOff, Mail, Lock, AlertCircle } from 'lucide-react';
import { Container, Row, Col, Card, Form, Button, Alert } from 'react-bootstrap';

const LoginPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { login, isAuthenticated, user } = useAuth();
  
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });
  
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');

  // Redirect if already authenticated
  useEffect(() => {
    if (isAuthenticated() && user) {
      const redirectPath = getRedirectPath(user.role);
      navigate(redirectPath, { replace: true });
    }
  }, [isAuthenticated, user, navigate]);

  // Show success message if redirected from registration
  useEffect(() => {
    if (location.state?.message) {
      setSuccessMessage(location.state.message);
      // Clear the message from location state
      window.history.replaceState({}, document.title);
    }
  }, [location.state]);

  const getRedirectPath = (role) => {
    switch (role) {
      case 'admin':
        return '/admin-dashboard';
      case 'student':
        return '/student-dashboard';
      case 'guest':
        return '/guest-dashboard';
      default:
        return '/';
    }
  };

  const validateForm = () => {
    if (!formData.email.trim()) {
      setError('Email is required');
      return false;
    }
    
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      setError('Please enter a valid email address');
      return false;
    }
    
    if (!formData.password) {
      setError('Password is required');
      return false;
    }
    
    return true;
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear error when user starts typing
    if (error) {
      setError('');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }
    
    setLoading(true);
    setError('');
    
    try {
      const result = await login(formData.email.trim().toLowerCase(), formData.password);
      
      if (result.success) {
        const redirectPath = getRedirectPath(result.user.role);
        navigate(redirectPath, { replace: true });
      } else {
        setError(result.error);
      }
    } catch (error) {
      setError('An unexpected error occurred. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-vh-100 d-flex align-items-center justify-content-center position-relative overflow-hidden" 
         style={{background: 'linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #f0f9ff 100%)'}}>
      {/* Modern Background Pattern */}
      <div className="position-absolute w-100 h-100 overflow-hidden">
        {/* Geometric shapes - responsive sizing */}
        <div className="position-absolute bg-primary opacity-20 rounded-3xl rotate-45 animate-pulse" 
             style={{top: '10%', left: '10%', width: '80px', height: '80px', animationDelay: '0s'}}></div>
        <div className="position-absolute bg-info opacity-20 rounded-circle animate-bounce" 
             style={{top: '20%', right: '15%', width: '60px', height: '60px', animationDelay: '0.5s'}}></div>
        <div className="position-absolute bg-success opacity-20 rounded-3xl animate-pulse" 
             style={{bottom: '20%', left: '15%', width: '100px', height: '100px', animationDelay: '1s'}}></div>
        <div className="position-absolute bg-warning opacity-20 rounded-circle animate-bounce" 
             style={{bottom: '10%', right: '10%', width: '70px', height: '70px', animationDelay: '1.5s'}}></div>
        
        {/* Subtle grid */}
        <div className="position-absolute w-100 h-100 opacity-20" style={{
          backgroundImage: `url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23059669' fill-opacity='0.05'%3E%3Cpath d='M20 20c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10zm10 0c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10z'/%3E%3C/g%3E%3C/svg%3E")`
        }}></div>
      </div>

      {/* Main Content Container */}
      <Container className="position-relative" style={{zIndex: 10}}>
        <Row className="justify-content-center">
          <Col xs={12} sm={10} md={8} lg={6} xl={5}>
            {/* Header Section */}
            <div className="text-center mb-4 mb-md-5">
              {/* Modern Logo */}
              <div className="mx-auto d-flex align-items-center justify-content-center mb-3 mb-md-4 shadow-lg" 
                   style={{
                     width: '80px', 
                     height: '80px', 
                     background: 'linear-gradient(135deg, #10b981, #0d9488)',
                     borderRadius: '20px',
                     transition: 'transform 0.3s ease'
                   }}
                   onMouseEnter={(e) => e.target.style.transform = 'scale(1.05)'}
                   onMouseLeave={(e) => e.target.style.transform = 'scale(1)'}>
                <svg className="text-white" width="32" height="32" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
              </div>
              
              <h1 className="display-6 fw-bold text-dark mb-2">
                Welcome Back
              </h1>
              <p className="lead text-muted">
                Sign in to SmartDoc
              </p>
            </div>

            {/* Login Form Card */}
            <Card className="border-0 shadow-lg" style={{
              background: 'rgba(255, 255, 255, 0.95)',
              backdropFilter: 'blur(20px)',
              borderRadius: '20px'
            }}>
              <Card.Body className="p-4 p-md-5">
                <Form onSubmit={handleSubmit}>
                  {/* Success Message */}
                  {successMessage && (
                    <Alert variant="success" className="border-0 rounded-3 mb-4" style={{background: 'rgba(34, 197, 94, 0.1)'}}>
                      <div className="d-flex align-items-center">
                        <svg className="text-success me-2" width="20" height="20" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                        </svg>
                        <span className="fw-medium">{successMessage}</span>
                      </div>
                    </Alert>
                  )}

                  {/* Email Field */}
                  <Form.Group className="mb-3">
                    <Form.Label className="fw-semibold text-dark">Email Address</Form.Label>
                    <div className="position-relative">
                      <div className="position-absolute top-50 start-0 translate-middle-y ms-3">
                        <Mail className="text-muted" size={20} />
                      </div>
                      <Form.Control
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleInputChange}
                        placeholder="Enter your email"
                        className="ps-5"
                        style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                      />
                    </div>
                  </Form.Group>

                  {/* Password Field */}
                  <Form.Group className="mb-4">
                    <Form.Label className="fw-semibold text-dark">Password</Form.Label>
                    <div className="position-relative">
                      <div className="position-absolute top-50 start-0 translate-middle-y ms-3">
                        <Lock className="text-muted" size={20} />
                      </div>
                      <Form.Control
                        type={showPassword ? 'text' : 'password'}
                        name="password"
                        value={formData.password}
                        onChange={handleInputChange}
                        placeholder="Enter your password"
                        className="ps-5 pe-5"
                        style={{borderRadius: '12px', border: '2px solid #e5e7eb'}}
                      />
                      <Button
                        type="button"
                        variant="link"
                        className="position-absolute top-50 end-0 translate-middle-y me-3 p-0"
                        onClick={() => setShowPassword(!showPassword)}
                        style={{border: 'none', background: 'none'}}
                      >
                        {showPassword ? (
                          <EyeOff className="text-muted" size={20} />
                        ) : (
                          <Eye className="text-muted" size={20} />
                        )}
                      </Button>
                    </div>
                  </Form.Group>

                  {/* Error Message */}
                  {error && (
                    <Alert variant="danger" className="border-0 rounded-3 mb-4" style={{background: 'rgba(239, 68, 68, 0.1)'}}>
                      <div className="d-flex align-items-center">
                        <AlertCircle className="text-danger me-2" size={20} />
                        <span className="fw-medium">{error}</span>
                      </div>
                    </Alert>
                  )}

                  {/* Submit Button */}
                  <Button
                    type="submit"
                    disabled={loading}
                    className="w-100 py-3 mb-3 fw-semibold border-0"
                    style={{
                      background: 'linear-gradient(135deg, #10b981, #0d9488)',
                      borderRadius: '12px',
                      boxShadow: '0 4px 15px rgba(16, 185, 129, 0.3)'
                    }}
                  >
                    {loading ? (
                      <>
                        <div className="spinner-border spinner-border-sm me-2" role="status">
                          <span className="visually-hidden">Loading...</span>
                        </div>
                        Signing in...
                      </>
                    ) : (
                      <>
                        Sign In
                        <svg className="ms-2" width="16" height="16" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                        </svg>
                      </>
                    )}
                  </Button>

                  {/* Register Link */}
                  <div className="text-center">
                    <p className="text-muted mb-0">
                      Don't have an account?{' '}
                      <Link
                        to="/register"
                        className="text-decoration-none fw-semibold"
                        style={{color: '#10b981'}}
                      >
                        Create one here
                      </Link>
                    </p>
                  </div>
                </Form>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default LoginPage;
